.sb-theme-rust {
  // Prism Theme Colors
  --sbt-theme-color-10: var(--sbt-rust-10);
  --sbt-theme-color-20: var(--sbt-rust-20);
  --sbt-theme-color-30: var(--sbt-rust-30);
  --sbt-theme-color-40: var(--sbt-rust-40);
  --sbt-theme-color-50: var(--sbt-rust-50);
  --sbt-theme-color-60: var(--sbt-rust-60);
  --sbt-theme-color-70: var(--sbt-rust-70);
  --sbt-theme-color-80: var(--sbt-rust-80);
  --sbt-theme-color-90: var(--sbt-rust-90);
  --sbt-theme-color-100: var(--sbt-rust-100);

  // Header colors
  --sbt-color-header-dark: var(--sbt-theme-color-60);
  --sbt-color-header-darker: var(--sbt-theme-color-70);
  --sbt-color-header-light: var(--sbt-theme-color-40);
  --sbt-color-header-lighter: var(--sbt-theme-color-30);
}
